.navbar {
    background: #0f2027;
    color: white;
    padding: 1rem 2rem;
    position: relative;
}

.navbar-brand {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.hamburger {
    font-size: 2rem;
    cursor: pointer;
    display: none;
    color: white;
}

.nav-links {
    list-style: none;
    display: flex;
    gap: 1.5rem;
    margin: 0;
    padding: 0;
}

.nav-links li a {
    color: white;
    text-decoration: none;
    font-size: 1rem;
}

/* Mobile styles */
@media (max-width: 768px) {
    .hamburger {
        display: block;
        padding-left: 160px;
    }

    .nav-links {
        display: none;
        flex-direction: column;
        background-color: #203a43;
        position: absolute;
        top: 100%;
        right: 0;
        width: 60%;
        border-radius: 0.5rem;
        padding: 1rem;
        z-index: 1000;

    }

    .nav-links.open {
        display: none;
    }

    .nav-links li {
        margin-bottom: 1rem;
    }

    .nav-links li:last-child {
        margin-bottom: 0;
    }
}
